import React from "react"
import "../styles/card.scss"
import config from "../../../data/SiteConfig"

export const CardLinks = props =>
  <div className='card_links' style={{ fontSize: props.data.h / 18 }}>
    <div className='card_date'>
      {props.data.date}
    </div>
    {props.data.git ? <a href={props.data.git}
                          aria-label='View the code on github'>
      <div className='git_link'> GIT</div>
    </a> : null}
    {props.data.web ? <a href={props.data.web}
                         aria-label='visit the website'>
      <div className='web_link'
           style={{ left: props.data.git ? "66%" : "33%" }}
      > WEB
      </div>
    </a> : null}
  </div>


