import React from "react"
import { graphql, StaticQuery } from "gatsby"
import { Helmet } from "react-helmet"
import { getClientDimensions } from "../utils/helpers"
import Footer from "../components/footer"
import ProjectCard from "../components/card/projectCard"
import Layout from "./layout"

export default class Portfolio extends React.Component {

  constructor(props) {
    super(props)
    this.state = { wHeight: 0, client: {}, over: -1 }
  }

  componentDidMount() {
    window.onresize = () => {
      this.setState({ wHeight: this.calculateCardHeight(), client: getClientDimensions() })
    }
    this.setState({ wHeight: this.calculateCardHeight(), client: getClientDimensions() })
  }

  calculateCardHeight = () => {
    let d = getClientDimensions()
    if (d.width > d.height) { // desktop or landscape
      if (d.height < 500) return d.height - 100
      return (d.height - 100) / 2.2
    }
    return d.height / 3
  }



  showProjects = (data) => {
    return data.map(node => {
      return <ProjectCard project={node.node} key={Math.random()} h={this.state.wHeight} c={this.state.client}/>
    })
  }

  render() {
    return (
    <Layout>
      <div className='portfolio_container container-fluid'>
        <Helmet title={"portfolio"} meta={[
          { name: "description", content: "A list of projects built by Catalin" },
          { name: "keywords", content: "projects, portfolio, Javascript, React" },
        ]}>
          <html lang="en"/>
        </Helmet>
        <main className='row'>
          <StaticQuery
            query={graphql`
              query {
                allDataJson {
                edges {
                  node {
                    fields {
                      slug
                    }
                    id,
                    date,
                    description,
                    git_link,
                    name,
                    story,
                    web_link,
                    media,
                    media_pre,
                    techs {
                        id,
                        description,
                        name,
                        pic_url,
                        tech_type,
                        url
                    }
                  }
                }
                totalCount
              }
            }
          `}
            render={data => this.showProjects(data.allDataJson.edges)}
          />
          <Footer/>
        </main>
      </div>
    </Layout>
    )
  }
}

