import React from "react"
import "../styles/card.scss"

export const CardHeader = props =>
  <div className='card_header'>
    <div className='card_title'> {props.data.title} </div>
    <div className='card_techs'>
      {
        props.data.techs && props.data.techs.map(t => {
          return (
            <div className='single_tech' key={Math.random()}>
              <img className='single_tech_img' src={t.pic_url} alt={t.name} title={t.name} />
            </div>
          )
        })
      }
    </div>
    <div className='card_border'>

    </div>
  </div>

